import i18n from './i18n/i18n'
export default {
    register: (resources) => {
        let modules = {};
        resources.map((resource) => {
            let state = {
                one: {},
                all: [],
                meta: {
                    current_page: 1
                },
                links: {},
                loaded: false,
                headers: [],
                errors: []
            };
            let getters = {
                all: state => state.all,
                one: state => state.one,
                headers: state => state.headers

            };
            let actions = {
                async index({
                    commit
                }, params) {
                    const response = await this.$axios.get(`/${resource.name}`, {
                        params: {
                            page: state.meta.current_page,
                            ...params
                        }
                    });
                    if (response.data.data.length > 0) {
                        let headers = (response.data.data[0].headers || Object.keys(response.data.data[0])).filter(i => !i.includes('_id')).map((header) => {
                            let value = header;
                            console.log(header);
                            // console.log(response.data.data[0][header]);
                            if (response.data.data[0][header]?.list_identifiers) {
                                value = response.data.data[0][header].list_identifiers.find(i => i.includes(header)) || header;
                            }
                            return {
                                text: i18n.t(header),
                                value
                            };
                        });
                        headers.push({
                            text: '',
                            value: 'actions'
                        });
                        commit('setHeaders', headers);
                    }
                    commit('setMeta', response.data.meta);
                    commit('setAll', response.data.data);
                    commit('setLinks', response.data.links);
                    commit('loadedData');
                },
                async show({
                    commit
                }, one) {
                    const response = await this.$axios.get(`/${resource.name}/${one.id}`);
                    commit('setOne', response.data.data);
                },
                async update({
                    commit
                }, data) {
                    // let parent_id = data[resource.parent + '_id'];
                    if (data.is_file) {
                        let form_data = new FormData();
                        Object.keys(data).map(key => {
                            form_data.append(key, data[key])
                        });
                        data = form_data;
                    }
                    try {

                        const response = await this.$axios.put(`/${resource.name}/${data.id}`, data);
                        commit('setOne', response.data);
                    } catch (exception) {
                        console.log(exception)
                    }
                },
                async store({
                    commit
                }, data) {
                    if (data.id) {
                        return this.dispatch(`${resource.name}/update`, data);
                    }
                    let parent_id = data[resource.parent + '_id'];
                    if (data.is_file) {
                        console.log('aaa')
                        let form_data = new FormData();
                        Object.keys(data).map(key => {
                            form_data.append(key, data[key])
                        });
                        data = form_data;
                    }
                    try {
                        const response = await this.$axios.post(`${resource.parent?'/'+resource.parent+'/'+parent_id:''}/${resource.name}`, data);
                        commit('setOne', response.data);
                        return response.data.data;
                    } catch (ex) {
                        let errors = (ex.response.data.errors);
                        if (errors) {
                            commit('setErrors', errors, {
                                root: true
                            });
                        }
                    }
                },
                async delete({
                    commit
                }, data) {
                    const response = await this.$axios.delete(`/${resource.name}/${data.id}`, data);
                    commit('setOne', response.data);
                },
                changeCurrentPage({
                    commit
                }, current_page) {
                    commit('setCurrentPage', current_page);
                },
                flipPage({
                    commit,
                    dispatch
                }) {
                    commit('goNext');
                    dispatch('index');
                }

            };
            let mutations = {
                setAll: (state, all) => state.all = all,
                pushData: (state, data) => state.all = state.all.concat(data),
                setOne: (state, one) => state.one = one,
                setMeta: (state, meta) => state.meta = meta,
                goNext: (state) => state.meta.current_page += 1,
                setLinks: (state, links) => state.links = links,
                loadedData: (state) => state.loaded = true,
                setHeaders: (state, headers) => state.headers = headers,
                setErrors: (state, errors) => state.errors = errors
            }
            modules[resource.module_name || resource.name] = {
                namespaced: true,
                state,
                getters,
                actions,
                mutations
            };
        });
        return modules;
    }
}
